import React from "react";
import {NavLink} from "react-router-dom";
import style from "./Header.module.css"

function Header() {
    return (
        <div className={style.container}>
            <div className={style.pre}>
                <NavLink to={"/pre-junior"} className={style.link}> Pre Jr </NavLink>
            </div>
            <div className={style.jr}>
                <NavLink to={"/junior"} className={style.link}> Jr </NavLink>
            </div>
            <div className={style.jrPl}>
                <NavLink to={"/juniorPlus"} className={style.link}> Jr Plus </NavLink>
            </div>
        </div>
    );
}

export default Header;
