import React, {useState} from "react";
import Affairs from "./Affairs";
import s from "./Affairs.module.css"

// types
export type AffairPriorityType =  "high" | "middle" | "low"

export type AffairType = {
    id: number
    name: string
    priority: string
};
export type FilterType = "all" | AffairPriorityType;



// constants
const defaultAffairs: Array<AffairType> = [
    {id: 1, name: "React", priority: "high"},
    {id: 2, name: "anime", priority: "low"},
    {id: 3, name: "games", priority: "low"},
    {id: 4, name: "work", priority: "high"},
    {id: 5, name: "html & css", priority: "middle"},
    ];

// pure helper functions
export const filterAffairs = (affairs: Array<AffairType>, filter: FilterType): Array<AffairType> => {
    if (filter === "all") {
        return affairs;
    }else if (filter === "high") {
       return  affairs.filter(af => af.priority === "high")
    }else if (filter === "middle"){
        return  affairs.filter(af => af.priority === "middle")
    } else if (filter === "low"){
       return  affairs.filter(af => af.priority === "low")
    }else return affairs
};

export const deleteAffair = (affairs:Array<AffairType> , id: number): Array<AffairType> => {
    return affairs.filter( af => af.id !== id)
};

function HW2() {
    const [affairs, setAffairs] = useState<Array<AffairType>>(defaultAffairs);
    const [filter, setFilter] = useState<FilterType>("all");

    const filteredAffairs = filterAffairs(affairs, filter);

    const deleteAffairCallback = (id: number) => setAffairs(deleteAffair(affairs, id));

    return (
        <div className={s.main_div}>

            <h3> Home Works 2</h3>


            <Affairs
                data={filteredAffairs}
                setFilter={setFilter}
                deleteAffairCallback={deleteAffairCallback}
            />


            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeAffairs/>*/}

        </div>
    );
}

export default HW2;
